
.hos-filter {
  position: absolute;
  width: 100%;
  top: 40px;
  z-index: 100;
}
.hos-filter .title-wrapper {
  width: 100%;
  height: 40px;
  background: #fff;
  border-bottom: 1px solid #dbd8d8;
}
.hos-filter .title-wrapper .ul-title-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.hos-filter .title-wrapper .ul-title-wrapper .li-filter-title {
  height: 40px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 40px;
  position: relative;
}
.hos-filter .title-wrapper .ul-title-wrapper .li-filter-title:after {
  display: block;
  position: absolute;
  top: 9px;
  right: 0;
  bottom: 9px;
  border-left: 1px solid #dbd8d8;
  content: ' ';
}
.hos-filter .title-wrapper .ul-title-wrapper .li-filter-title.current:after {
  display: none;
}
.hos-filter .title-wrapper .ul-title-wrapper .div-title-wrapper {
  margin: auto;
}
.filter-mask {
  position: absolute;
  width: 100%;
  bottom: 0;
  top: 40px;
  z-index: 50;
  background: rgba(7,17,27,0.8);
}


