
.sort-detail {
  position: fixed;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 10;
}
.sort-detail .drug-list {
  width: 100%;
  position: absolute;
  top: 80px;
  bottom: 0;
  background: #fff;
  overflow: hidden;
}
.sort-detail .drug-list .drug-info-wrapper {
  width: 100%;
  height: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 10px;
  position: relative;
}
.sort-detail .drug-list .drug-info-wrapper:after {
  display: block;
  position: absolute;
  left: 10px;
  bottom: 0;
  right: 10px;
  border-top: 1px solid #dbd8d8;
  content: ' ';
}
.sort-detail .drug-list .drug-info-wrapper .avatar-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 60px;
          flex: 0 0 60px;
  margin: 10px 0;
}
.sort-detail .drug-list .drug-info-wrapper .content-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 10px 0 10px 10px;
}
.sort-detail .drug-list .drug-info-wrapper .content-wrapper .title {
  line-height: 15px;
  height: 30px;
  font-size: 15px;
  font-weight: 500;
  color: #363636;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.sort-detail .drug-list .drug-info-wrapper .content-wrapper .des {
  height: 13px;
  font-size: 13px;
  line-height: 13px;
  color: #a2a2a2;
  width: 200px;
  margin-top: 7px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.sort-detail .drug-list .drug-info-wrapper .content-wrapper .extra-content {
  position: relative;
  color: #f46a75;
  margin-top: 10px;
  height: 23px;
  line-height: 23px;
  font-size: 15px;
}
.sort-detail .drug-list .drug-info-wrapper .content-wrapper .extra-content .submit-demand {
  position: absolute;
  right: 3px;
  height: 23px;
  border-radius: 3px;
  padding: 0 3px;
  border: 1px solid #f46a75;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.slide-enter-active,
.slide-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}


