
.single {
  background: #fff;
  width: 100%;
  height: 200px;
}
.single .single-content-wrapper {
  position: relative;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.single .single-content-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  border-top: 1px solid #dbd8d8;
  content: ' ';
}
.single .single-content-wrapper.selected {
  color: #0294df;
}
.single .single-content-wrapper .single-div {
  left: 50px;
  position: absolute;
}


