
.hybrid {
  background: #fff;
  width: 100%;
  height: 100px;
  padding: 20px;
}
.hybrid .qualification {
  height: 40px;
}
.hybrid .qualification .qualification-text {
  font-size: 15px;
  font-weight: 500;
}
.hybrid .select-item {
  margin-left: 15px;
}
.hybrid .select-item .select-item-li {
  font-size: 15px;
  font-weight: 500;
  padding: 5px;
  display: inline-block;
  margin-left: 10px;
}
.hybrid .select-item .select-item-li.selected {
  color: #0294df;
  border: 1px solid #0294df;
  border-radius: 10%;
}


