
.double {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  width: 100%;
  height: 300px;
}
.double .left-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
  background: #f0f0f0;
}
.double .left-wrapper .left-menu {
  position: relative;
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.double .left-wrapper .left-menu:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  border-top: 1px solid #dbd8d8;
  content: ' ';
}
.double .left-wrapper .left-menu.current {
  background: #fff;
}
.double .right-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  overflow: hidden;
}
.double .right-wrapper .item-data-wrapper {
  position: absolute;
  left: 10px;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.double .right-wrapper .item-data-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  border-top: 1px solid #dbd8d8;
  content: ' ';
}
.double .right-wrapper .item-data-wrapper.selected {
  color: #0294df;
}


