
.header2 {
  left: 0;
  top: 0;
  width: 100%;
  z-index: 50;
  height: 40px;
  background: #f6f7f7;
  position: relative;
}
.header2 .left-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  height: 39px;
  width: 39px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.header2 .left-wrapper .left-icon {
  width: 10px;
  height: 14px;
  display: inline-block;
  background-size: 10px 14px;
  background-repeat: no-repeat;
  background-image: url("pharmacy_blue_back@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header2 .left-wrapper .left-icon {
    background-image: url("pharmacy_blue_back@3x.png");
}
}
.header2 .right-wrapper {
  position: absolute;
  right: 0;
  top: 0;
  height: 39px;
  width: 39px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.header2 .right-wrapper .right-icon {
  width: 40px;
  height: 20px;
  display: inline-block;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
  color: #00a5f9;
  font-size: 15px;
  text-align: center;
  line-height: 20px;
}
.header2 .right-wrapper .right-icon.pharmacy_titlebar_icon_point_normal {
  background-image: url("pharmacy_titlebar_icon_point_normal@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header2 .right-wrapper .right-icon.pharmacy_titlebar_icon_point_normal {
    background-image: url("pharmacy_titlebar_icon_point_normal@3x.png");
}
}
.header2 .search-wrapper {
  position: absolute;
  left: 39px;
  right: 39px;
  padding: 3px;
  margin: 5.5px 0;
  background: #fff;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border: 1px solid #878787;
}
.header2 .search-wrapper .search-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("pharmacy_sousuo@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header2 .search-wrapper .search-icon {
    background-image: url("pharmacy_sousuo@3x.png");
}
}
.header2 .search-wrapper .input-text {
  position: absolute;
  display: inline-block;
  margin-left: 5px;
  outline: none;
  font-size: 15px;
  width: 80%;
}
.header2 .oneline {
  position: absolute;
  bottom: 0;
}


