
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  background: #f4f4f4;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.tab .tab-item > a {
  font-size: 10px;
  color: #4d555d;
}
.tab .tab-item > a.router-link-active {
  color: #00a5f9;
}
.tab .tab-item .tab-item-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 48px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.tab .tab-item .tab-item-wrapper .icon {
  display: inline-block;
  width: 23px;
  height: 23px;
  background-size: 23px 23px;
  background-repeat: no-repeat;
}
.tab .tab-item .tab-item-wrapper .icon.check0 {
  background-image: url("pharmacy_pocket_pharmacies_selected@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.tab .tab-item .tab-item-wrapper .icon.check0 {
